

.statistics{
display: flex;
align-items: center;
flex-direction: column;

max-width: 500px;

background-color: #ffffff;
margin-top: 20px;
margin-left: auto;
 margin-right: auto;
outline: 1px solid #D5F8FB;
}

.title{
    margin: 0;
    padding: 20px 0;
}

.stat-list{
   width: 100%; 
   display: flex;
   list-style: none; 

   margin: 0;
   padding: 0;
   }

   .item{
        width: calc(100% / 5);
       display: inherit;
       align-items: center;
       flex-direction: column;
       gap: 5px;
       padding: 15px;
   }

   .label, 
   .percentage{
    color: #D5F8FB;
    font-weight: bold;
   }